 -->
<h3>Radio 单选</h3>
<hr>
<h4>使用方式</h4>
<pre v-pre><code v-pre class="language-javascript">
<span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { Radio, RadioGroup } <span class="hljs-keyword">from</span> <span class="hljs-string">'vvmui'</span>

Vue.component(Radio)
Vue.component(RadioGroup)

</code></pre>
<h4>基础用法</h4>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Radio</span>&gt;</span>单选框<span class="hljs-tag">&lt;/<span class="hljs-name">Radio</span>&gt;</span>

</code></pre>
<h4>type类型</h4>
<p><code v-pre>type</code>类型提供<code v-pre>default</code>、<code v-pre>errer</code>、<code v-pre>success</code>、<code v-pre>warning</code>四种可选，默认<code v-pre>default</code></p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Radio-Group</span> <span class="hljs-attr">inline</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"errer"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"errer"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"success"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"success"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"warning"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">Radio-Group</span>&gt;</span>

</code></pre>
<h4>配合RadioGroup使用</h4>
<p><code v-pre>RadioGroup</code>组件通过<code v-pre>inline</code>可把<code v-pre>Radio</code>设置为行内块元素</p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Radio-Group</span> <span class="hljs-attr">inline</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"单选框1"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"单选框2"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"单选框3"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">Radio-Group</span>&gt;</span>

</code></pre>
<h4>自定义图标</h4>
<p><code v-pre>Radio</code>组件通过<code v-pre>icon</code>属性也可自定义选中图标</p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Radio-Group</span> <span class="hljs-attr">inline</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"iconselected"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"单选框1"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"iconselected"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"单选框2"</span> /&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"iconselected"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"单选框3"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">Radio-Group</span>&gt;</span>

</code></pre>
<h4>配合icon使用</h4>
<p>通过插槽可自定义一些显示内容</p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">Radio-Group</span> <span class="hljs-attr">inline</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"单选框1"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"iconaccount"</span> /&gt;</span>
        单选框1
    <span class="hljs-tag">&lt;/<span class="hljs-name">Radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"单选框2"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Icon</span> <span class="hljs-attr">icon</span>=<span class="hljs-string">"iconcamera"</span> /&gt;</span>
        单选框2
    <span class="hljs-tag">&lt;/<span class="hljs-name">Radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">Radio-Group</span>&gt;</span>

</code></pre>
<h4>禁用</h4>
<p>通过<code v-pre>disabled</code>属性可禁用</p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">disabled</span>&gt;</span>单选框<span class="hljs-tag">&lt;/<span class="hljs-name">Radio</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">disabled</span>&gt;</span>单选框<span class="hljs-tag">&lt;/<span class="hljs-name">Radio</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">value</span>: <span class="hljs-literal">true</span>
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>使用RadioGroup禁用</h4>
<p>也可通过<code v-pre>RadioGroup</code>组件的<code v-pre>disabled</code>控制所有<code v-pre>Radio</code>组件的禁用状态，<code v-pre>type</code>属性和<code v-pre>icon</code>属性也可这样操作</p>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">Radio-Group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"label"</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">inline</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"单选框1"</span> /&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"单选框2"</span> /&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">Radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"单选框3"</span> /&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">Radio-Group</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">label</span>: <span class="hljs-string">"单选框1"</span>
    };
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h3>API</h3>
<h5>Radio props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>绑定值</td>
<td>String/Number/Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>type</td>
<td>不同的类型对应不同的主题色彩</td>
<td>String</td>
<td><code v-pre>default</code> <code v-pre>errer</code> <code v-pre>success</code> <code v-pre>warning</code></td>
<td>default</td>
</tr>
<tr>
<td>label</td>
<td>Radio 的 value</td>
<td>String/Number/Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>icon</td>
<td>自定义Radio选中的icon</td>
<td>String</td>
<td>无</td>
<td>iconradioactive</td>
</tr>
</tbody>
</table>
<h3>API</h3>
<h5>RadioGroup props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>v-model</td>
<td>绑定值</td>
<td>String/Number/Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>type</td>
<td>设置该属性可控制Radio的type属性</td>
<td>String</td>
<td><code v-pre>default</code> <code v-pre>errer</code> <code v-pre>success</code> <code v-pre>warning</code></td>
<td>无</td>
</tr>
<tr>
<td>inline</td>
<td>是否开启行内块</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>disabled</td>
<td>设置该属性可控制Radio的disabled属性</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>icon</td>
<td>设置该属性可控制Radio的icon属性</td>
<td>String</td>
<td>无</td>
<td>无</td>
</tr>
</tbody>
</table>
<h5>Radio solt</h5>
<table>
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>无</td>
<td>自定义主体内容</td>
</tr>
</tbody>
</table>
<h5>Radio Events</h5>
<table>
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>Radio变化时触发，嵌套RadioGroup组件使用时无效</td>
<td>选中的label值</td>
</tr>
</tbody>
</table>
<h5>RadioGroup Events</h5>
<table>
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>绑定值变化时触发</td>
<td>选中的label值</td>
</tr>
</tbody>
</table>
<h5>更新日志</h5>
<table>
<thead>
<tr>
<th style="text-align:center">版本</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">内容</th>
</tr>
</thead>
<tbody></tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      value: true
    };
  }
};
</script>


